import React from 'react';

class About extends React.Component {
    render() {
        return (
            <div className="aboutus card">
                <p>
                    Somos amantes de la centenaria bebida refrescante conocida como Agua de Horchata, tradicional bebida mexicana que suele acompaar nuestros tacos o arracheras. A pesar de su fcil y conocida preparacin, en Horchata.Club nos dimos la tarea de probar las mejores horchatas de la Ciudad de Mxico, documentarlas y clasificarlas.
                </p>
                <p className="question">
                    Qu es el agua de horchata?
                </p>
                <p>
                    Forma parte de las tradicionales aguas frescas mexicanas, junto con el agua de jamaica, tamarindo, limn y otras frutas tpicas. El agua de horchata se prepara mezclando harina de arroz, azcar blanca, canela, leche en polvo, vainilla y en ocasiones almendras, coco, y semillas de morro; Aunque la receta puede variar segn la regin y el gusto personal.
                </p>
                <p className="question">
                    Dnde encontramos el agua de horchata?
                </p>
                <p>
                    Bebida enraizada en la cultura gastronmica mexicana, la encontramos en cualquier restaurante que se jacte de mexicano; comnmente acompaada con tacos en das veraniegos, aunque no falta su presencia en el repertorio de la abuela y fiestas infantiles (y no tan infantiles).   
                </p>
            </div>
        );
    }
}

export default About;



